<template>
  <div class="login-container">
    <div class="login-card">
      <h1 class="login-title"></h1>
      <form @submit.prevent="logIn" class="login-form">
        <div class="form-group">
          <label for="username" class="form-label"></label>
          <input type="text" id="username" v-model.trim="username" placeholder="  ." class="form-input">
        </div>
        <div class="form-group">
          <label for="password" class="form-label"></label>
          <input type="password" id="password" v-model.trim="password" placeholder=" ." class="form-input">
        </div>
        <div class="form-group submit-group">
          <input type="submit" value="Log In" class="submit-button">
        </div>
      </form>
    </div>
  </div>
</template>

<script setup>
import { ref } from 'vue'
import { useCounterStore } from '@/stores/counter'

const username = ref(null)
const password = ref(null)

const store = useCounterStore()

const logIn = function () {
  const payload = {
    username: username.value,
    password: password.value
  }
  store.logIn(payload)
}
</script>

<style>
/*   */
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh; /*    */
  font-family: Arial, sans-serif;
  padding: 1rem;
  box-sizing: border-box;
}

/*   */
.login-card {
  background: #fff;
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
  width: 400px;
  max-width: 90%;
  height: 450px; /*   */
  text-align: center;
  overflow: hidden; /*   */
  display: flex;
  flex-direction: column; /*     */
  justify-content: space-between; /*    */
}

/*   */
.login-title {
  font-size: 2.5rem;
  color: #333;
  margin-bottom: 2.5rem;
}

/*   */
.login-form {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between; /*       */
}

/*    */
.form-group {
  margin-bottom: 1rem;
}

/*   */
.form-label {
  display: block;
  font-weight: bold;
  color: #555;
  margin-bottom: 0.5rem;
  text-align: left;
}

/*   */
.form-input {
  width: 100%;
  padding: 0.7rem;
  border: 1px solid #4b47c857;
  border-radius: 5px;
  font-size: 0.9rem;
  box-sizing: border-box;
}

/*   */
.submit-group {
  text-align: right; /*    */
}

.submit-button {
  background: #6c63ff;
  color: #fff;
  border: none;
  padding: 0.7rem 1.5rem;
  border-radius: 5px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: background 0.3s;
}

.submit-button:hover {
  background: #4b47c8;
}
</style>
