<template>
  <div class="login-container">
    <div class="login-card">
      <h1 class="login-title"></h1>
      <form @submit.prevent="signUp" class="login-form">
        <!--    -->
        <div class="form-group profile-container">
          <div class="profile-details">
            <label for="profilePicture" class="form-label"> </label>
            <button type="button" class="upload-button" @click="triggerFileInput"> </button>
            <input type="file" id="profilePicture" ref="fileInput" @change="onFileChange" class="hidden-file-input">
          </div>
          <div v-if="profilePreview" class="form-preview">
            <img :src="profilePreview" alt="Profile Preview" class="profile-preview" />
          </div>
        </div>

        <!--    -->
        <div class="form-group">
          <label for="username" class="form-label"></label>
          <input type="text" id="username" v-model.trim="username" class="form-input">
        </div>
        <div class="form-group">
          <label for="password1" class="form-label"></label>
          <input type="password" id="password1" v-model.trim="password1" class="form-input">
        </div>
        <div class="form-group">
          <label for="password2" class="form-label"> </label>
          <input type="password" id="password2" v-model.trim="password2" class="form-input">
        </div>
        <div class="form-group submit-group">
          <input type="submit" value="Sign Up" class="submit-button">
        </div>
      </form>
    </div>
  </div>
</template>

<script setup>
import { ref } from 'vue';
import { useCounterStore } from '@/stores/counter';

const username = ref('');
const password1 = ref('');
const password2 = ref('');
const profilePicture = ref(null);
const profilePreview = ref(null);
const fileInput = ref(null);

const store = useCounterStore();

const onFileChange = (event) => {
  const file = event.target.files[0];
  if (file) {
    profilePicture.value = file;
    profilePreview.value = URL.createObjectURL(file); //  URL 
  }
};
const signUp = async () => {
  const formData = new FormData();
  formData.append('username', username.value);
  formData.append('password1', password1.value);
  formData.append('password2', password2.value);
  if (profilePicture.value) {
    formData.append('profile_picture', profilePicture.value);
  }

  console.log("DEBUG: FormData content:");
  for (let [key, value] of formData.entries()) {
    console.log(`${key}:`, value);
  }

  try {
    await store.signUp(formData); //  FormData 
    alert(' !');
  } catch (error) {
    console.error(' :', error);
    alert(' !');
  }
}
const triggerFileInput = () => {
  fileInput.value.click(); //     
};

</script>

<style scoped>
/*   */
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
  font-family: Arial, sans-serif;
  padding: 1rem;
  box-sizing: border-box;
}

/*   */
.login-card {
  background: #fff;
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
  width: 400px;
  max-width: 90%;
  height: auto;
  text-align: center;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

/*   */
.login-title {
  font-size: 2.5rem;
  color: #333;
  margin-bottom: 2.5rem;
}

/*   */
.login-form {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

/*    */
.profile-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
}

/*   */
.profile-details {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

/*   */
.form-label {
  font-weight: bold;
  color: #555;
  margin-bottom: 0.5rem;
}

/*    */
.hidden-file-input {
  display: none;
}

/*    */
.upload-button {
  background-color: #6c63ff;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: background 0.3s;
}

.upload-button:hover {
  background-color: #4b47c8;
}

/*   */
.profile-preview {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 50%;
  border: 2px solid #4b47c857;
  margin-right: 65px;
}

/*    */
.form-group {
  margin-bottom: 1rem;
}

/*   */
.form-input {
  width: 100%;
  padding: 0.7rem;
  border: 1px solid #4b47c857;
  border-radius: 5px;
  font-size: 0.9rem;
  box-sizing: border-box;
}

/*   */
.submit-group {
  text-align: right;
}

.submit-button {
  background: #6c63ff;
  color: #fff;
  border: none;
  padding: 0.7rem 1.5rem;
  border-radius: 5px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: background 0.3s;
}

.submit-button:hover {
  background: #4b47c8;
}
</style>
