  display: flex;
  align-items: center;
  gap: 5px; /*   */
  margin-bottom: 3px; /*     */
  cursor: pointer;
  /* border-bottom: 1px solid #ddd; */
  padding-bottom: 3px;
}

.profile-image {
  width: 40px; /*   */
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
  margin-bottom: 80px;
}

