  display: flex;
  flex-direction: column;
  gap: 2px; /* , ,    */
}

.username {
  font-weight: bold;
  font-size: 18px; /*   */
  color: #000; /*   */
}

.created-at {
  color: #000;
  font-size: 12px;
}

.rating {
  font-size: 15px;
  color: #f39c12;
  font-weight: bold;
}

/*     */
.review-content .review-text {
  font-size: 14px;
  margin-bottom: 10px;
  line-height: 1.4;
  color: #000; /*   */
}

/*  :   */
.movie-card {
  display: flex;
  flex-direction: row;
  gap: 10px;
  align-items: flex-start;
  cursor: pointer;
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
  transition: transform 0.2s;
  background-color: #d3d3d3; /*   */
