  padding: 10px;
}
.movie-card:hover {
  transform: scale(1.02);
}

.poster-image {
  width: 80px; /*   */
  height: 120px;
  object-fit: cover;
}

.movie-info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0;
  gap: 5px;
  color: #000; /*   */
}

.movie-title {
  font-size: 14px; /*   */
  font-weight: bold;
  margin: 0;
  color: #000; /*   */
}

.movie-genres {
  display: flex;
  gap: 5px;
  flex-wrap: wrap; /*     */
  color: #000; /*    */
}

.genre {
  background-color: #f1f1f1;
  padding: 2px 5px;
  font-size: 12px;
  border-radius: 4px;
  color: #000; /*    */
}

.movie-overview {
  font-size: 12px;
  color: #000; /*   */
  margin-top: 10px;
  line-height: 1.4;
}

.movie-rating {
  font-weight: bold;
  color: #f39c12;
}

/*    */
/*     */
.like-container {
  display: flex;
  align-items: center;
  gap: 20px; /*      */
