}

/*   */
.like-button:hover .like-icon,
.like-button:hover .liked-icon {
  color: #e74c3c; /* hover    */
}

.like-button:hover .like-count {
  color: #e74c3c; /*   hover     */
}

.like-icon,
.liked-icon {
  font-size: 20px; /*    */
