<template>
  <div class="ranking-container">
    <h1 class="text-center tracking-in-expand-fwd my-4">MOVIE MONSTER RANKING</h1>
    <!--     -->
    <div class="info-container">
      <a href="#" class="rankinfo" @click="showModal = true">
        <span></span>
        <span></span>
        <span></span>
        <span></span>
          
      </a>
    </div>



    <table class="ranking-table">
      <thead>
        <tr>
          <th class="with-border"></th>
          <th></th>
          <th></th>
          <th></th>
          <th> </th>
          <th> </th>
          <th> </th>
          <th> </th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="(user, index) in rankings"
          :key="user.id"
          :class="{ 'text-focus-in': animatedIndexes.includes(index), hidden: !animatedIndexes.includes(index) }"
        >
          <!--  -->
          <td class="with-border">
            <div v-if="index === 0">
              <img src="@/assets/gold.png" alt="Gold Medal" class="medal-icon" />
            </div>
            <div v-else-if="index === 1">
              <img src="@/assets/silver.png" alt="Silver Medal" class="medal-icon" />
            </div>
            <div v-else-if="index === 2">
              <img src="@/assets/bronze.png" alt="Bronze Medal" class="medal-icon" />
            </div>
            <div v-else>{{ index + 1 }}</div>
          </td>
          <!--  -->
          <td>
            <div class="profile-picture">
              <img :src="`http://127.0.0.1:8000${user.profile_picture}`" alt=" " class="profile-img" />
            </div>
          </td>
          <!--  -->
          <td>
            <RouterLink :to="{ name: 'ProfileView', params: { username: user.username } }">
              {{ user.username }}
            </RouterLink>
          </td>
          <!--  -->
          <td>
            <div class="rank-display">
              <img :src="getRankImage(user.rank_title)" :alt="user.rank_title" class="rank-icon" />
              {{ user.points }}
            </div>
          </td>
          <!--   -->
          <td>{{ user.articles_count }}</td>
          <!--   -->
          <td>{{ user.likes_count }}</td>
          <!--   -->
          <td>{{ user.followers_count }}</td>
          <!--   -->
          <td>
            <div v-if="user.recommended_movie">
              <RouterLink :to="{ name: 'MovieDetail', params: { id: user.recommended_movie.movie.id } }">
                {{ user.recommended_movie.movie.title }}
              </RouterLink>
            </div>
            <div v-else>
              <span>X</span>
            </div>
          </td>
        </tr>
      </tbody>
    </table>

    <!-- RankInfoModal  -->
    <RankInfoModal v-if="showModal" :isVisible="showModal" @close="showModal = false" />
  </div>
</template>

<script setup>
import { ref, computed, onMounted } from "vue";
import { useCounterStore } from "@/stores/counter";
import RankInfoModal from "@/components/RankInfoModal.vue";

// Pinia  
const store = useCounterStore();
const rankings = computed(() => store.rankings); //    
const showModal = ref(false);

//  
const animatedIndexes = ref([]); //    

const applySequentialAnimation = () => {
  rankings.value.forEach((_, index) => {
    setTimeout(() => {
      animatedIndexes.value.push(index);
    }, index * 300); //   300ms  
  });
};

//    
import bronzeRank from "@/assets/BronzeRank.png";
import silverRank from "@/assets/SilverRank.png";
import goldRank from "@/assets/GoldRank.png";
import platinumRank from "@/assets/PlatinumRank.png";
import diamondRank from "@/assets/DiamondRank.png";

const getRankImage = (rankTitle) => {
  switch (rankTitle) {
    case "Bronze": return bronzeRank;
    case "Silver": return silverRank;
    case "Gold": return goldRank;
    case "Platinum": return platinumRank;
    case "Diamond": return diamondRank;
    default: return bronzeRank;
  }
};

//    
onMounted(() => {
  if (!store.isLogin) {
    alert(" .");
    store.router.push({ name: "LogInView" });
  } else {
    store.fetchRankings().then(() => {
      applySequentialAnimation(); //   
    });
  }
});
</script>


<style scoped>
.text-center {
  color: #4da0ff;
  font-size: 2rem;
  font-weight: bold;
  animation: tracking-in-expand-fwd 0.8s cubic-bezier(0.215, 0.610, 0.355, 1.000) both; /*   */
}

@keyframes tracking-in-expand-fwd {
  0% {
    letter-spacing: -0.5em;
    transform: translateZ(-700px);
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    transform: translateZ(0);
    opacity: 1;
  }
}

/*   */
.ranking-container {
  max-width: 1000px; /*    */
  margin: 40px auto;
  text-align: center;
  color: #f0f8ff; /*    */
  overflow-x: auto; /*    */
  padding-bottom: 10px; /*       */
}

.info-container {
  text-align: right;
  margin-bottom: 15px;
}

/*   */
.ranking-table {
  width: 1000px; /*    */
  border-collapse: separate;
  border-spacing: 0;
  background-color: #1a2a44; /*   -   */
  border: 1px solid #4169e1; /*   */
  table-layout: fixed; /*   */
}

.ranking-table th,
.ranking-table td {
  padding: 10px;
  text-align: center;
  background-color: #243653; /*   -   */
  color: #e6f0ff; /*   -   */
  border-bottom: 1px solid #4169e1; /*    */
  white-space: normal; /*    */
  word-wrap: break-word; /*    */
  overflow-wrap: break-word; /*     */
  font-size: 0.9rem; /*    */
}

.ranking-table th {
  font-weight: bold;
  background-color: #2a4066; /*   -    */
  color: #f0faff; /*    */
  border-bottom: 1px solid #6090d0; /*   */
  font-size: 1rem; /*    */
}

.ranking-table th.with-border,
.ranking-table td.with-border {
  border-right: 1px solid #4169e1; /*   */
}

.ranking-table tbody tr {
  border-bottom: 1px solid #4169e1; /*   */
}

/*    */
.ranking-table th:first-child,
.ranking-table td:first-child {
  width: 5%; /*  */
}

.ranking-table th:nth-child(2),
.ranking-table td:nth-child(2) {
  width: 10%; /*  */
}

.ranking-table th:nth-child(3),
.ranking-table td:nth-child(3) {
  width: 15%; /*  */
}

.ranking-table th:nth-child(4),
.ranking-table td:nth-child(4) {
  width: 10%; /*  */
}

.ranking-table th:nth-child(5),
.ranking-table td:nth-child(5) {
  width: 10%; /*   */
}

.ranking-table th:nth-child(6),
.ranking-table td:nth-child(6) {
  width: 10%; /*   */
}

.ranking-table th:nth-child(7),
.ranking-table td:nth-child(7) {
  width: 10%; /*   */
}

.ranking-table th:last-child,
.ranking-table td:last-child {
  width: 20%; /*   */
}

.medal-icon {
  width: 32px;
  height: 32px;
}

/*   */
.profile-img {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  object-fit: cover;
  border: 0.5px solid #87cefa; /*    */
}

.rank-icon {
  width: 40px;
  height: 40px;
  object-fit: cover;
}

/*    */
.ranking-table a {
  color: #e6f0ff; /*      */
  text-decoration: none; /*   */
  font-weight: normal;
}

.ranking-table a:hover {
  color: #87cefa; /*     */
  text-decoration: underline; /*     */
}

/*  */
.text-focus-in {
  animation: text-focus-in 1s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
}

@keyframes text-focus-in {
  0% {
    filter: blur(12px);
    opacity: 0;
  }
  100% {
    filter: blur(0px);
    opacity: 1;
  }
}

/*  */
.hidden {
  opacity: 0;
}


/*    */
.rankinfo {
  position: relative;
  display: inline-block;
  padding: 15px 20px;
  font-size: 14px; /*    */
    margin: 20px 0; /*   */
  color: #4169e1;
  text-decoration: none;
  text-transform: uppercase;
  transition: 0.5s;
  letter-spacing: 1px;
  overflow: hidden;
  /* margin-right: 20px; */
  margin-top: 40px;
  margin-bottom: 0px;
}

.rankinfo:hover {
  background: #4169e1;
  color: #050801;
  box-shadow: 0 0 5px #4169e1, 0 0 25px #4169e1, 0 0 50px #4169e1,
    0 0 200px #4169e1;
  -webkit-box-reflect: below 1px linear-gradient(transparent, #0005);
}

.rankinfo span {
  position: absolute;
  display: block;
}

.rankinfo span:nth-child(1) {
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, #4169e1);
  animation: animate1 1s linear infinite;
}

@keyframes animate1 {
  0% {
    left: -100%;
  }
  50%,
  100% {
    left: 100%;
  }
}

.rankinfo span:nth-child(2) {
  top: -100%;
  right: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(180deg, transparent, #4169e1);
  animation: animate2 1s linear infinite;
  animation-delay: 0.25s;
}

@keyframes animate2 {
  0% {
    top: -100%;
  }
  50%,
  100% {
    top: 100%;
  }
}

.rankinfo span:nth-child(3) {
  bottom: 0;
  right: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(270deg, transparent, #4169e1);
  animation: animate3 1s linear infinite;
  animation-delay: 0.5s;
}

@keyframes animate3 {
  0% {
    right: -100%;
  }
  50%,
  100% {
    right: 100%;
  }
}

.rankinfo span:nth-child(4) {
  bottom: -100%;
  left: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(360deg, transparent, #4169e1);
  animation: animate4 1s linear infinite;
  animation-delay: 0.75s;
}

@keyframes animate4 {
  0% {
    bottom: -100%;
  }
  50%,
  100% {
    bottom: 100%;
  }
}
</style>
