<template>
  <div class="container">
    <!--     -->
    <div class="d-flex justify-content-between align-items-center mb-4">
      <h2 class="text-center">{{ sectionTitle }} </h2>
      <button class="btn-back" @click="goBack">
        <span class="arrow">&lt;</span> 
      </button>
      <!-- <button class="btn btn-secondary" @click="goBack"></button> -->
    </div>

    <!--   -->
    <div class="grid-container">
      <div
        class="card"
        v-for="movie in movies"
        :key="movie.movie_id"
        @click="goToDetail(movie.movie_id)"
      >
        <img
          :src="getFullPosterUrl(movie.poster_url)"
          class="card-img-top"
          :alt="movie.title"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted } from "vue";
import { useRoute, useRouter } from "vue-router";
import axios from "axios";

export default {
  setup() {
    const route = useRoute();
    const router = useRouter();
    const section = ref(route.params.section);
    const movies = ref([]);

    //   
    const fetchMovies = async () => {
      try {
        const apiEndpoint =
          section.value === "popular"
            ? "/popular.json"
            : section.value === "recent"
            ? "/recent.json"
            : "/upcoming.json";

        const response = await axios.get(apiEndpoint);
        movies.value = response.data.map((item) => ({
          movie_id: item.fields.movie_id,
          title: item.fields.title,
          poster_url: item.fields.poster_url,
        }));
      } catch (error) {
        console.error("Error fetching movies:", error);
      }
    };

    //   
    const sectionTitle = computed(() => {
      return section.value === "popular"
        ? ""
        : section.value === "recent"
        ? ""
        : "";
    });

    //    
    const goToDetail = (movieId) => {
      router.push({ name: "MovieDetail", params: { id: movieId } });
    };

    //   URL 
    const getFullPosterUrl = (posterUrl) =>
      `https://image.tmdb.org/t/p/w500${posterUrl}`;

    //  
    const goBack = () => {
      router.go(-1); //   
    };

    onMounted(fetchMovies);

    return {
      sectionTitle,
      movies,
      goToDetail,
      getFullPosterUrl,
      goBack, //   
    };
  },
};
</script>

<style scoped>
.container {
  width: 100%; /*    */
  padding: 0; /*    */
  margin-top: 20px;
}

.grid-container {
  display: flex;
  flex-wrap: wrap;
  row-gap: 2rem; /*   */
  column-gap: 1.5rem; /*   */
  justify-content: start;
}

.card {
  width: 240px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  overflow: hidden;
  text-align: center;
  transition: transform 0.2s ease-in-out;
  background-color: transparent; /*     */
}

.card:hover {
  transform: scale(1.05);
}

.card-img-top {
  width: 100%;
  height: 360px;
  object-fit: cover;
}

.btn-back {
  display: inline-flex;
  align-items: center; /*    */
  color: #e5e5e5; /*    */
  font-size: 16px; /*   */
  font-weight: 500; /*   */
  background: none; /*   */
  border: none; /*   */
  cursor: pointer; /*   */
  padding: 5px 10px; /*  */
  text-decoration: none; /*   */
  transition: color 0.3s ease-in-out; /*   */
}

.btn-back .arrow {
  margin-right: 5px; /*    */
  font-size: 18px; /*   */
  color: inherit; /*    */
}

.btn-back:hover {
  color: #ffffff; /*     */
}
</style>
