<template>
  <div>
    <!-- <h3> </h3> -->

    <!--   -->
    <div class="tabs-container">
      <button @click="setSortOrder('popular')" :class="{ active: sortOrder === 'popular' }"></button>
      <button @click="setSortOrder('recent')" :class="{ active: sortOrder === 'recent' }"></button>
    </div>

    <div v-if="store.articles.length === 0">
      <p>  .</p>
    </div>

    <div v-if="errorMessage" class="error-message">{{ errorMessage }}</div>
    <div v-if="articles.length > 0">
      <ArticleListItem
        v-for="article in articles"
        :key="article.id"
        :article="article"
        @update-article="updateArticle"
      />
    </div>
  </div>
</template>

<script setup>
import ArticleListItem from '@/components/ArticleListItem.vue'
import { useCounterStore } from '@/stores/counter'
import { ref, onMounted, computed } from 'vue'
import { useRouter } from 'vue-router'
import axios from 'axios'

const searchQuery = ref('')
const errorMessage = ref('')
const router = useRouter()

const store = useCounterStore()

const articles = computed(() => store.articles);
console.log('vvv',articles)

const sortOrder = ref('recent') //   : 

//       
const setSortOrder = (order) => {
  sortOrder.value = order
  store.getSortedArticles(order) //   
}

const updateArticle = (updatedArticle) => {
  const index = store.articles.findIndex((article) => article.id === updatedArticle.id);
  if (index !== -1) {
    store.articles[index] = { ...updatedArticle };
    console.log("Updated article in parent component:", store.articles[index]);
  }
};



//      
onMounted(async () => {
  try {
    // //        
    // if (!store.articles.length) {
      await store.getSortedArticles(sortOrder.value);
    
    console.log('dfdf', articles.value)
  } catch (err) {
    console.error("Error loading articles:", err);
    errorMessage.value = "    .";
  }
});

</script>


<style scoped>
/*   */
.tabs-container {
  display: flex;
  justify-content: flex-start;
  gap: 25px; /*        */
  border-bottom: 2px solid #ff9800; /*     */
  padding-bottom: 10px; /*      */
  margin-bottom: 25px; /*     */
}

.tabs-container button {
  padding: 10px 20px;
  border: none;
  background: none;
  cursor: pointer;
  font-size: 18px;
  color: #888888; /*   :  */
  transition: color 0.3s, border-bottom 0.3s;
  font-weight: 600;
}

.tabs-container button.active {
  color: #ffffff; /*   :  */
  border-bottom: 3px solid #ffffff; /*     */
}

/*    */
.error-message {
  color: #ff4d4d; /*   */
  margin-top: 20px;
}

/*      */
.empty-message {
  color: #cccccc; /*     */
  font-size: 18px;
  text-align: center; /*   */
  margin-top: 30px;
}
</style>