<template>
  <div
    ref="modalRef"
    class="modal fade"
    id="youtubeTrailerModal"
    tabindex="-1"
    aria-labelledby="youtubeTrailerLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-lg">
      <div class="modal-content custom-modal-content">
        <div class="modal-header custom-modal-header">
          <h5 class="modal-title custom-modal-title" id="youtubeTrailerLabel">
            {{ movieTitle }}  
          </h5>
          <!--   -->
          <button
            type="button" 
            class="btn-close custom-close-btn"
            data-bs-dismiss="modal"
            aria-label="Close"
            @click="stopVideo"
          >
            
          </button>
        </div>
        <div class="modal-body custom-modal-body">
          <div v-if="isLoading" class="spinner-border text-primary" role="status">
            <span class="visually-hidden">Loading...</span>
          </div>
          <iframe
            v-else-if="videoData"
            class="trailer-iframe"
            :src="videoData"
            frameborder="0"
            allowfullscreen
          ></iframe>
          <div v-else>
            <p>     .</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, onBeforeUnmount } from "vue";
import axios from "axios";

const props = defineProps({
  movieTitle: String,
});

const videoData = ref("");
const isLoading = ref(false);
const modalRef = ref(null);
const youtubeKey = import.meta.env.VITE_YOUTUBE_API_KEY; // API 

const stopVideo = () => {
  videoData.value = ""; //  
};

//   
const fetchTrailer = async () => {
  isLoading.value = true;
  try {
    const response = await axios.get("https://www.googleapis.com/youtube/v3/search", {
      params: {
        key: youtubeKey,
        part: "snippet",
        type: "video",
        q: `${props.movieTitle}  OR trailer`,
        maxResults: 1,
      },
    });

    const videoId = response.data.items[0]?.id?.videoId || null;
    if (videoId) {
      videoData.value = `https://www.youtube.com/embed/${videoId}?autoplay=1&mute=1`;
    } else {
      videoData.value = null;
    }
  } catch (error) {
    console.error("YouTube API  :", error);
    videoData.value = null;
  } finally {
    isLoading.value = false;
  }
};

//  
onMounted(() => {
  if (modalRef.value) {
    modalRef.value.addEventListener("show.bs.modal", fetchTrailer);
    modalRef.value.addEventListener("hidden.bs.modal", stopVideo);
  }
});

onBeforeUnmount(() => {
  if (modalRef.value) {
    modalRef.value.removeEventListener("show.bs.modal", fetchTrailer);
    modalRef.value.removeEventListener("hidden.bs.modal", stopVideo);
  }
});
</script>

<style scoped>
/*    */
.custom-modal-content {
  background: linear-gradient(145deg, #1e1e1e, #2c2c2c); /*   */
  border-radius: 10px;
  color: white; /*   */
  padding: 15px;
}

.custom-modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

/*    */
.custom-close-btn {
  background: none;
  border: none;
  color: white;
  font-size: 2rem; /*    */
  cursor: pointer;
  transition: color 0.3s ease;
}

.custom-close-btn:hover {
  color: #fff; /*       */
}

.custom-modal-title {
  font-size: 1.5rem;
  font-weight: bold;
}

.custom-modal-body {
  padding: 20px;
  text-align: center;
}

/* YouTube     */
.trailer-iframe {
  width: 100%;
  height: 550px; /*    */
  display: block;
  border-radius: 8px; /*     */
  margin: 0 auto;
}

/*   */
.spinner-border {
  margin: 50px auto;
}

/*    */
.modal-content {
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.6);
}
</style>
