<template>
  <div class="background-container"></div>
  <div class="app-wrapper">
    <header>
      <nav class="navbar">
        <div class="container">
          <img class="logoimage" src="/assets/logos/Monster Logo.png" alt="">
          <!--   -->
          
          <RouterLink :to="{ name: 'HomeView' }" class="nav-link">HOME</RouterLink> |
          <RouterLink :to="{ name: 'MovieView' }" class="nav-link">MOVIE</RouterLink> |
          <RouterLink :to="{ name: 'ArticleView' }" class="nav-link">REVIEW</RouterLink> |
          <RouterLink :to="{ name: 'GameView' }" class="nav-link">GAME</RouterLink> |
          <RouterLink :to="{ name: 'RankView' }" class="nav-link">RANKING</RouterLink> |

          <RouterLink
            v-if="isLogin && user.username"
            :to="{ name: 'ProfileView', params: { username: user.username || '' } }"
            class="nav-link"
          >
            MY PROFILE
          </RouterLink>
          
          <div v-else>
            <RouterLink :to="{ name: 'LogInView' }" class="nav-link"></RouterLink>
            <RouterLink :to="{ name: 'SignUpView' }" class="nav-link"></RouterLink>
          </div>

          <!--   -->
          <div class="user-info" v-if="!isLoading && user.username">
            <div v-if="user.rank_title" class="rank-display">
              <img :src="getRankImage(user.rank_title)" :alt="user.rank_title" class="rank-icon" />
            </div> |
            {{ user.username }} | Points: {{ user.current_points }}
          </div>

          <form @submit.prevent="logOut" v-if="isLogin">
            <input type="submit" value="">
          </form>
        </div>
      </nav>
    </header>
    <main class="content">
      <!--   -->
      <div v-if="isLoading" class="loading">
        <p>Loading...</p>
      </div>
      <!--    -->
      <RouterView v-else />
    </main>

    <!--    -->
    <button v-if="showScrollToTop" class="scroll-to-top" @click="scrollToTop">
      
    </button>

    <footer class="footer">
      <div class="footer-container">
        <!--   -->
        <div class="footer-left">
          <p><strong>MovieMoster</strong> |   | <a href="mailto:rjs4013@naver.com">rjs4013@naver.com</a></p>
          <p>  | <a href="mailto:ghgghg96@naver.com">ghgghg96@naver.com</a> |  010-1234-5678</p>
        </div>

        <!--   -->
        <div class="footer-right">
          <p>Copyright &copy; 2024 by MovieMoster, Inc. All Rights Reserved.</p>
        </div>
      </div>
    </footer>
  </div>
</template>

<script setup>
import { computed, ref, watchEffect, onMounted } from "vue";
import { useCounterStore } from "@/stores/counter";

const store = useCounterStore();
const isLogin = computed(() => store.isLogin);
const user = computed(() => store.user);

const isLoading = ref(true); //   
const showScrollToTop = ref(false); //    

//   
import bronzeRank from "@/assets/BronzeRank.png";
import silverRank from "@/assets/SilverRank.png";
import goldRank from "@/assets/GoldRank.png";
import platinumRank from "@/assets/PlatinumRank.png";
import diamondRank from "@/assets/DiamondRank.png";

//    
const getRankImage = (rankTitle) => {
  switch (rankTitle) {
    case "Bronze":
      return bronzeRank;
    case "Silver":
      return silverRank;
    case "Gold":
      return goldRank;
    case "Platinum":
      return platinumRank;
    case "Diamond":
      return diamondRank;
    default:
      return bronzeRank; // 
  }
};

//  
const logOut = () => {
  store.logOut();
};

//   
watchEffect(async () => {
  if (store.token) {
    await store.fetchUser(); //   
    await store.fetchUserPoints(); //   
    isLoading.value = false; //  
  } else {
    isLoading.value = false; //      
  }
});

//    
const handleScroll = () => {
  showScrollToTop.value = window.scrollY > 300; //  300px    
};

//  
const scrollToTop = () => {
  window.scrollTo({ top: 0, behavior: "smooth" });
};

onMounted(() => {
  window.addEventListener("scroll", handleScroll);
});

</script>

<style scoped>
.scroll-to-top {
  position: fixed;
  bottom: 20px; /*   20px */
  right: 20px; /*   20px */
  width: 60px; /*   */
  height: 60px; /*   */
  background-color: #007bff; /*   */
  border: none;
  border-radius: 50%; /*   */
  color: #ffffff; /*   */
  font-size: 20px; /*   */
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  transition: transform 0.2s ease-in-out, background-color 0.3s ease;
}

.scroll-to-top:hover {
  background-color: #0056b3; /*    */
  transform: scale(1.1); /*   */
}

.scroll-to-top:active {
  transform: scale(0.9); /*     */
}

.a11y-hidden {
    overflow: hidden;
    clip: rect(1px, 1px, 1px, 1px);
    position: absolute;
    width: 1px;
    height: 1px;
}

form input[type="submit"] {
  background: linear-gradient(135deg, #ff5858, #f09819); /*   */
  border: none; /*    */
  color: white; /*   */
  padding: 10px 20px; /*   */
  font-size: 16px; /*   */
  font-weight: bold; /*   */
  border-radius: 25px; /*    */
  cursor: pointer; /*     */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2); /*   */
  transition: transform 0.2s ease, box-shadow 0.3s ease; /*  */
}

form input[type="submit"]:hover {
  transform: translateY(-2px); /*    */
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.3); /*      */
}

form input[type="submit"]:active {
  transform: translateY(0); /*     */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2); /*     */
}

.logoimage {
  width: 60px;
  height: 50px;
  object-fit: cover;
}

.nav-link {
  position: relative;
  display: inline-block;
  padding: 10px 15px;
  font-size: 16px;
  font-weight: bold;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;
  z-index: 1;
}

.nav-link::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(224, 47, 240, 0.2), rgba(0, 0, 0, 0));
  border-radius: 5px;
  transform: scale(0.8);
  opacity: 0;
  transition: all 0.4s ease-in-out;
  z-index: -1; /*     */
}

.nav-link:hover {
  color: #e02ff0; /*      */
}

.nav-link:hover::after {
  transform: scale(1);
  opacity: 1;
  background: linear-gradient(135deg, rgba(224, 47, 240, 0.4), rgba(0, 0, 0, 0));
  box-shadow: 0 0 15px rgba(224, 47, 240, 0.5), 0 0 20px rgba(224, 47, 240, 0.3);
}


</style>
