<template>
  <div class="review-container">
    <h1 class="title"> </h1>

    <button @click="goBack" class="cancel-btn">  </button>

    <!-- '   '  -->
    <div class="select-movie-btn-container">
      <button @click="openMovieSelectModal" class="select-movie-btn">   </button>
    </div>

    <!--    -->
    <div v-if="showMovieModal" class="modal">
      <div class="modal-content">
        <h2> </h2>
        <input v-model="searchQuery" @input="searchMovies" placeholder="  " />
        <ul>
          <li v-for="movie in searchResults" :key="movie.id">
            {{ movie.title }}
            <button @click="selectMovie(movie)"></button>
          </li>
        </ul>
        <button @click="closeMovieModal" class="close-modal-btn"></button>
      </div>
    </div>

    <!--       -->
    <div v-if="selectedMovie" class="selected-movie">
      <h3> : {{ selectedMovie.title }}</h3>
      <img :src="selectedMoviePoster" alt="  " class="poster-image" />
    </div>

    <!--   -->
    <div class="stars">
      <div
        v-for="n in 10"
        :key="n"
        class="star"
        :class="{ filled: n <= hoverRating || n <= selectedRating }"
        @mouseover="hoverStar(n)"
        @mouseleave="clearHover"
        @click="selectRating(n)"
      ></div>
    </div>
    <p class="rating-text"> : {{ selectedRating }} / 10</p>

    <!--    -->
    <form @submit.prevent="createArticle" class="form-container">
      <div class="form-group">
        <input
          type="text"
          v-model.trim="title"
          class="form-input styled-input"
          placeholder=" "
        />
      </div>
      <div class="form-group">
        <textarea
          v-model.trim="content"
          class="form-textarea styled-textarea"
          placeholder=" "
        ></textarea>
      </div>
      <button type="submit" class="submit-btn"></button>
    </form>
  </div>
</template>

<script setup>
import { ref } from "vue";
import { useCounterStore } from "@/stores/counter";
import { useRouter } from "vue-router";

//  
const title = ref("");
const content = ref("");
const searchQuery = ref("");
const searchResults = ref([]);
const selectedMovie = ref(null); //   
const selectedMoviePoster = ref(null); //    URL
const errorMessage = ref("");
const showMovieModal = ref(false);
const hoverRating = ref(0);
const selectedRating = ref(0);

const store = useCounterStore();
const router = useRouter();

const goBack = () => {
  router.go(-1);
};

//  /
const openMovieSelectModal = () => (showMovieModal.value = true);
const closeMovieModal = () => {
  showMovieModal.value = false;
  searchQuery.value = "";
  searchResults.value = [];
};

//  
const searchMovies = async () => {
  if (!searchQuery.value.trim()) {
    searchResults.value = [];
    return;
  }

  try {
    const response = await store.searchMovies(searchQuery.value.trim());
    searchResults.value = response;
  } catch (error) {
    console.error("Error searching movies:", error);
    errorMessage.value = "  .";
  }
};

//  
const selectMovie = (movie) => {
  selectedMovie.value = movie; //    
  selectedMoviePoster.value = `https://image.tmdb.org/t/p/w500${movie.poster_url}`; //  URL 
  closeMovieModal();
};

//   
const hoverStar = (value) => (hoverRating.value = value);
const clearHover = () => (hoverRating.value = 0);
const selectRating = (rating) => (selectedRating.value = rating);

//  
const isSubmitting = ref(false);

const createArticle = async () => {
  if (isSubmitting.value) return; //    
  isSubmitting.value = true;

  if (!selectedMovie.value) {
    alert("   .");
    isSubmitting.value = false;
    return;
  }

  try {
    const newArticle = await store.createArticle({
      title: title.value,
      content: content.value,
      poster_url: selectedMoviePoster.value,
      rating: selectedRating.value,
      movie: selectedMovie.value.id,
    });

    //     
    store.articles.unshift(newArticle);  //  
    await store.fetchUserPoints(); //     
    alert("  .");
    router.push({ name: "ArticleView" });
  } catch (error) {
    console.error("Error creating article:", error);
    alert("  .");
  } finally {
    isSubmitting.value = false; //     
  }
};

</script>


<style scoped>
.review-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  background: #121212;
  color: #fff;
  border-radius: 8px;
}
/*   */
.form-container {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

/*     */
.styled-input {
  width: 100%;
  padding: 15px;
  font-size: 18px;
  font-weight: bold;
  border: 1px solid #ccc;
  border-radius: 6px;
  background: #222; /*   */
  color: #fff;
  box-sizing: border-box;
}

.styled-input::placeholder {
  color: #888; /*   */
  font-weight: normal;
}

/*     */
.styled-textarea {
  width: 100%;
  padding: 15px;
  font-size: 16px;
  line-height: 1.5;
  border: 1px solid #ccc;
  border-radius: 6px;
  background: #222; /*   */
  color: #fff;
  min-height: 150px; /*   */
  resize: vertical; /*     */
  box-sizing: border-box;
}

.styled-textarea::placeholder {
  color: #888; /*   */
  font-weight: normal;
}

/*     */
.styled-input:focus,
.styled-textarea:focus {
  outline: none;
  border-color: #ff9800; /*     */
  box-shadow: 0 0 5px rgba(255, 152, 0, 0.7); /*    */
}

/*   */
.submit-btn {
  background: #ff9800;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}

.submit-btn:hover {
  background: #e88e00;
}

.title {
  text-align: center;
  font-size: 24px;
  margin-bottom: 20px;
}

.cancel-btn {
  background: #ff4d4d;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  margin-bottom: 20px;
}

.select-movie-btn-container {
  text-align: center;
  margin-bottom: 20px;
}

.select-movie-btn {
  background: #ff9800;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6); /*    */
  display: flex;
