  width: 400px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  color: #fff;
  position: relative; /*      */
  display: flex;
  flex-direction: column; /*     */
  gap: 15px; /*   */
}
.modal-content h2 {
  font-size: 18px;
  margin-bottom: 20px;
  color: #fff;
