  border: 1px solid #555;
  border-radius: 4px;
  background: #1f1f1f; /*    */
  color: #fff;
  font-size: 14px;
}

.modal-content input::placeholder {
  color: #888;
}

.modal-content ul {
  list-style: none;
  padding: 0;
  margin: 0;
  max-height: 200px; /*     */
  overflow-y: auto; /*     */
}

.modal-content ul li {
  display: flex;
  justify-content: space-between; /*       */
  align-items: center;
  padding: 10px 0;
  border-bottom: 1px solid #444; /*    */
  font-size: 16px;
}

.modal-content ul li:last-child {
  border-bottom: none; /*     */
}

.modal-content ul li button {
  background: #ff9800; /*    */
  color: white;
