}

.modal-content ul li button:hover {
  background: #e88e00; /*      */
}

.close-modal-btn {
  align-self: flex-end; /*     */
  background: #d32f2f; /*    */
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}

.close-modal-btn:hover {
  background: #b71c1c; /*     */
}
.selected-movie {
  text-align: center;
  margin: 20px 0;
}

.poster-image {
  max-width: 200px;
  height: auto;
  display: block;
  margin: 10px auto;
  border-radius: 8px;
}

.stars {
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
}

.star {
  width: 24px;
  height: 24px;
  background: url("@/assets/images/gray-star.png") no-repeat center;
  background-size: contain;
  cursor: pointer;
}

.star.filled {
  background: url("@/assets/images/yellow-star.png") no-repeat center;
  background-size: contain;
}

.rating-text {
  text-align: center;
}

.form-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-label {
  font-size: 16px;
  margin-bottom: 5px;
}

.form-input,
.form-textarea {
  padding: 10px;
